@use '../abstracts/index' as *;

.order {
  text-align: left;
  padding: 2rem 1.5rem;
  background: url(../assets/plan/mobile/bg-order-summary.png) no-repeat
    center/cover;
  border-radius: 8px;

  @include desktop {
    grid-column: 2 / 3;
  }

  &--title {
    margin-bottom: 0.75rem;
  }

  span {
    &.color-light {
      color: color(color-light);
      margin: 0;
      margin-right: 0.5rem;
    }
  }
}
